import Request from '@/plugins/luch-request/luch-request/index.js'
import {
	BASE_URL
} from "@/utils/common.js"


const http = new Request({
	baseURL: BASE_URL,
	timeout: 100000,
	header: {
		'ContentType': 'application/json'
	},
});

// 
http.interceptors.request.use((config) => { // async await 
	let jwttoken = uni.getStorageSync('jwttoken');

	if (jwttoken) {
		config.header.jwttoken = jwttoken
	}
	return config
}, config => { // async await 
	return Promise.reject(config)
})

// 
http.interceptors.response.use((response) => {
	/*  async await */
	let code = response.data.code //

	switch (code) {
		case 1005: { //
			uni.redirectTo({ //
				url: '/pages/login/login'
			});
			break;
		}

		case 1006: { //token
			uni.setStorageSync('jwttoken', response.data.data.jwttoken)

			// 
			let config = response.config
			config.header.jwttoken = response.data.data.jwttoken

			return http.request(config) //
		}


		default: { //
			// console.log(1000)
		}

	}

	return response
}, (response) => {
	/*   statusCode !== 200*/
	if (response.errMsg === "request:fail timeout") {
		console.log("")
		return Promise.reject(response)
	}
	console.log("")
	return Promise.reject(response)
})

export default http
